import Vue from 'vue'


//
Vue.filter('levelTxt',function (val) {
  let returnVal = '';
  const num = Number(val);
  if(num == 1){
    returnVal =''
  }
  else if(num == 2){
    returnVal =''
  }
  else if(num == 3){
    returnVal =''
  }
  else{
    returnVal =num;
  }

  return returnVal;
});

//
Vue.filter('licenceType',function (val) {
  let returnVal = '';
  const num = Number(val);
  if(num == 0){
    returnVal =''
  }
  else if(num == 1){
    returnVal =''
  }
  else if(num == 2){
    returnVal =''
  }
  else if(num == 3){
    returnVal =' '
  }
  else if(num == 4){
    returnVal =''
  }
  else if(num == 5){
    returnVal =''
  }
  else if(num == 6){
    returnVal =''
  }
  else if(num == 7){
    returnVal =''
  }
  else if(num == 8){
    returnVal =''
  }

  return returnVal;
});





